import { SRLWrapper } from "simple-react-lightbox";
import { options } from "../options";
import { imagesCollection } from "../imagesCollection";
import Masonry, {ResponsiveMasonry} from 'react-responsive-masonry';

const columnsCountBreakPoints = { 350: 1, 750: 2, 900: 3 };

function Wrapper() {
  return (
    // <SRLWrapper options={options}>
    //   <div className="row justify-content-around p-2" data-masonry='{"percentPosition": true }'>
    //     {imagesCollection.map((image) => (
    //       <div className="col-md-3" key={image.id}>
    //         <img
    //           src={image.image}
    //           className="img-thumbnail shadow-lg m-2"
    //           alt={image.desc}
    //         />
    //       </div>
    //     ))}
    //   </div>
    // </SRLWrapper>

    <SRLWrapper options={options}>
    <ResponsiveMasonry columnsCountBreakPoints={columnsCountBreakPoints}>
  <Masonry gutter={10}>
            {imagesCollection.map((image)  => (
              <img width= "100%" className="img-thumbnail shadow-lg m-2" src={image.image}  alt={image.desc} key={image.id} />
            ))}
          </Masonry>
          </ResponsiveMasonry>
    </SRLWrapper>

  );
}
export default Wrapper;
